.navbar {
  background:  #0a92ac;
  height: 100%;
  display: flex;
  /* justify-content: center;
  align-items: center; */
  font-size: 1.2rem;
  position: sticky;
  top: 0;
  z-index: 999;
 

 
}

.navbar-container {
  display: flex;

  align-items:flex-end;
  height: 20%;
  max-width: 1500px;
  


}

.navbar-logo {
  color: #fff;
  justify-self: start;
  margin-left: 5%;
  cursor: pointer;
  text-decoration: none;
  font-size: 2rem;
  display: flex;
  align-items: left;
  padding-right: 20%;
  text-align: left;

}

.navbar-title {
/* 
  flex-wrap: nowrap; 
  text-align: end;
  white-space: nowrap; */
  padding-left: 1rem;
  color: white;

}

.fa-typo3 {
  margin-left: 0.5rem;
  font-size: 1.8rem;
}

.nav-menu {
  display: grid;
  grid-template-columns: repeat(4, auto);
  grid-gap: 10px;
  list-style: none;
  text-align: center;
  width: 60vw;
  justify-content: end;
  vertical-align: text-bottom; 
  margin-right: 2rem;
  /* background-color:#0a92ac; */
  padding-left: 2rem;
  position: absolute;
  right: 0%;
  vertical-align: middle;
  text-align: center;
  margin-top: auto;

  
  }

  .nav-menu h2 {
    padding-top: 2rem;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  }

.nav-item {
  height: 80px;

  
}



.nav-links {
  color: #fff;
  display: flex;
  align-items: center;
  text-decoration: none;
  padding: 0.5rem 1rem;
  height: 100%;
}

.nav-links:hover {
  /* border-bottom: 4px solid #fff; */
  transition: all 0.2s ease-out;
  text-decoration: underline;
  text-decoration-color:white ;
}

.fa-bars {
  color:#0a92ac;
  background:inherit;
 
}

.nav-links-mobile {
  display: none;
}

.menu-icon {
  display: none;
  z-index: 6;
  padding-top: 0.3rem;
  background:initial
}

@media screen and (max-width: 960px) {
  .NavbarItems {
    position: relative;

  }

  .nav-item {
    font-size: 2vw;
    margin-top: 2rem;
  }

  .nav-menu {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 90vh;
    position: absolute;
    top: 2rem;
    left: -100%;
    opacity: 1;
    transition: all 0.5s ease;
    

  
  }

  .nav-menu.active {
    background: #0a92ac;
    left: 0;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
    vertical-align: top;
    padding-bottom: 100%;
    height:3500%;
    top: 100%;
    
  }

  .nav-links {
    text-align: center;
    padding: 2rem;
    width: 100%;
    display: table;
    
  }


  .nav-links:hover {

    color: #242424;
    background: inherit;

  }

  .navbar-logo {
    position: absolute;
    top: 0;
    left: 0;
    transform: translate(25%, 50%);
    z-index: -1;
  }

  .menu-icon {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
  }

  .fa-times {
    color: #fff;
    font-size: 2rem;
    background: inherit;
  }

  .nav-links-mobile {
    display: block;
    text-align: center;
    margin: 2rem auto;
    border-radius: 4px;
    width: 80%;
    text-decoration: none;
    font-size: 1.5rem;
    background-color: transparent;
    color: #fff;
    padding: 14px 20px;
    border: 1px solid #fff;
    transition: all 0.3s ease-out;
    
  }

 
}
